				this.total++;
			}else{
				this.total--;
			}
		},
		selectAll:function(){
			this.isAll = !this.isAll;
			if(this.isAll == true){
				this.total = 0;
				this.items.forEach(o=>{			
					o.isActive = true;
					this.total++
				})
			}else{
				this.total = 0;	
				this.items.forEach(o=>{
					o.isActive = false
				})
			}
		}
